.dropdown__icon,.dropdown__icon span{position:relative;align-items:center;z-index:999999}.dropdown-menu{z-index:999999;display:block}.active .dropdown-menu{display:block;z-index:999999}.dropdown__icon{display:flex;gap:1rem;cursor:pointer;transition:.8s cubic-bezier(.19, 1, .22, 1);color:var(--text-color);top:-0rem}.dropdown__icon.spin,.language__dropdown{transition:1s cubic-bezier(.16, 1, .3, 1)}.dropdown__icon:hover{cursor:pointer}.dropdown__icon.spin{position:relative;color:var(--container-color);transition-delay:0.3s;padding:.5rem}.dropdown__icon span{display:flex;justify-content:center;font-size:1.1rem;font-family:Satoshi-Bold;gap:.6rem}.dropdown__icon span i{font-size:1.3rem}.language__dropdown{position:absolute;display:flex;align-items:center;justify-content:center;right:5rem;width:20vw;height:5.4vh;padding:1rem;gap:2.5rem;background:var(--container-color);border-radius:0.3rem;box-shadow:0 4px 30px rgba(0,0,0,.1);backdrop-filter:blur(4.8px);-webkit-backdrop-filter:blur(4.8px);border:1px solid rgba(238,231,225,.3);z-index:99999999}.language__dropdown-link{display:flex;justify-content:space-between;align-items:center;gap:2rem}.language__dropdown span{font-size:1.1rem;font-family:Posterama;color:var(--title-color)}
